.App {
  margin: 10%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App_input{
  display: flex;
  align-items: center;
  font-size: 1.5rem;
}
.App_input_inputbox{
  display: flex;
  align-items: center;
}
.App_input_inputbox_target{
  width: 30%;
}

.App_input_inputbox_explication{
  width: 60%;
}

.App_modify{
  position: fixed;
  z-index: 10;
  top: 300px;
  background: white;
  box-shadow: 5px 5px 15px -3px rgb(0 0 0 / 70%);
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  font-size: 1.5rem;
}
.App_modify_inputbox{
  display: flex;
  align-items: center;
}
.App_modify_inputbox_target{
  width: 30%;
}

.App_modify_inputbox_explication{
  width: 60%;
}

.App_input_submit{
  display: flex;
  align-items: center;
}

.fa-xmark{
  margin-left: 10px;
}

input{
  border-radius: 5px;
  margin-right: 5px;
  border: 1px solid black;
}